class TransactionLimitHistoryPage extends React.Component {
    constructor(props) {
      	super(props);
		this.state = {
			open: false,
			tableData: []
		};
	}

	getTlHistory = async () => {
		const TLHISTORY_ENDPOINT = SERVER_URL+'/v1/api/app/admin/customer-request/limit-update/history';
		console.log(getToken());
		
		let config = {
			headers: {
				"client-key":"julklsjdmmaludnm01#",
				"Authorization": "Bearer "+getToken().token
			}
		}
		
		try {
			let response = await axios.get(TLHISTORY_ENDPOINT, config);
			
			if (response.status === 200) {
				console.log(response)
				return response.data.data.records;
			} else {
				//display error
				console.log('error');
				return [];
			}
		} catch (error) {
			console.log("the error")
			console.log(error);
			return [];
		}
	}

	componentDidMount(){
		console.log("component mounted");
		
		if (!this.state.tableData.length) {
			this.getTlHistory()
			.then(tableData => this.setState({tableData}))
			.catch(err => { /*...handle the error...*/});

			console.log("who is called");

			console.log(this.state.tableData);
			
        } else {
			console.log(this.state.tableData);
			
		}
	}
	
	render () {

		const pageTitle = location ? location.pathname.replace(/\//g, '') : ''

		/**auth();*/

		console.log("called");
		console.log(this.state.tableData);

		if (this.state.tableData.length == 0) {
			return (
				<Layout location={location} title={pageTitle}>
					<div className="n-container">
						<p className="n-headtext">TRANSACTION LIMIT HISTORY (0)</p>
						<p>All handled transaction limit</p>
						
						<div className="n-box">
							<img src="/images/safebox.png" className="n-img" />
							
							<p className="n-headtext">NO TRANSACTION LIMIT</p>
							<p className="n-center">No transaction limit has been handled</p>
						</div>
					</div>
				</Layout>
			)
		}

        return (
			<Layout location={location} title={pageTitle}>
				<div className="n-container">
					<p className="n-headtext">TRANSACTION LIMIT HISTORY ({this.state.tableData.length})</p>
                    <p>All handled transaction limit</p>
					
					<div className="n-filterrow">
						
						<Button className="n-primarybutton">SEND A NOTIFICATION</Button>
					</div>
	
					<PageTables
						headers={odata}
						rows={this.state.tableData}>
					</PageTables>
				</div>
			</Layout>
		);
    }
};

TransactionLimitHistoryPage.propTypes = {
	data: PropTypes.object.isRequired,
	location: PropTypes.object,
}

export default TransactionLimitHistoryPage